<template>
<main>
    <ListModal v-if="modalVisible1" @close1="modalVisible1 = false" :listdata="modalData1"/>
    <Header :header="content"/>
    <input type="text" placeholder="نام دستگاه ، نام ست  یا تاریخ مدنظر را جستجو نمایید ..." v-model="filter" style="width:500px;display:block;margin:10px auto;font-family:yekan;"/>
    <div class="container list">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام دستگاه</th>
                <th scope="col">شماره سیکل</th>
                <th scope="col">تاریخ</th>
                <th scope="col">ست های دریافتی</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in filteredRows" :key="item">
                <th scope="row">{{index+1}}</th>
                <td>{{item.name}}</td>
                <td>{{item.cycle}}</td>
                <td>{{item.date}}</td>
                <td><button class="btn btn-primary " style="padding:2px;font-size:0.8rem;" @click="openModal1(item)">مشاهده</button></td>
                </tr>
            </tbody>
        </table>
    </div>
 
</main>
</template>

<script>
import axios from 'axios'
import Header from '../../components/Header.vue'
import ListModal from '../../components/ListModal.vue'
export default {
    name:'DeviceStatus',
    components:{Header,ListModal},
    data() {
        return {
            content:'مشاهده سیکل کاری',
            filter:'',
            list:null,
            modalData1:null,
            modalVisible1: false
        }
    },
    created() {
        axios.get('../cycle.json')
        .then(({data})=>{
            this.list=data;
        })
    },
    computed: {
         filteredRows() {
                return this.list.filter(item => {
                const name = item.name.toString().toLowerCase();
                const parts = item.parts.toString();
                const date = item.date.toString();
                const searchTerm = this.filter.toLowerCase();

                return name.includes(searchTerm) ||
                parts.includes(searchTerm) ||
                date.includes(searchTerm) ;
            });
            
            
         }
    },
    methods: {
        openModal1(data) {
            this.modalData1 = data;
            this.modalVisible1 = true;
        },
        
        
    },
    
      
}
</script>

<style lang="scss" scoped>
.list{
        max-height: 57vh;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
        
                }
                    
        }
    }
</style>